* {
  box-sizing: border-box;
}
html, body {
  min-height: 100%;
}

body {
  background: url(./assets/images/bg.svg) no-repeat center top, linear-gradient(167.96deg, #f4f9ff 0%, #e0edfb 100%);
  /* background: linear-gradient(167.96deg, #f4f9ff 0%, #e0edfb 100%); */
  font-family: "Inter", "Open Sans", sans-serif;
}

svg {
  vertical-align: middle;
}


::placeholder {
  color: #6F91BC;
}


input {
  outline: none;
}

input {
  border: 1px solid #fff;
  /* border: 1px solid #151d5133; */
  border-radius: 10px;
  color: #151d51;
  font-size: 16px;
  font-weight: 400;
  line-height: 20px;
  padding: 10px 20px 10px 20px;
  width: 315px;
  height: 48px;
}

input[type="password"], input[type="text"] {
  padding: 10px 46px 10px 20px;
}

input:focus {
  /* border: 1px solid #151d51; */
  border: 1px solid #6F91BC;
  color: #4A4E71;
}

input:focus::placeholder {
  color: #4A4E71;
}

.valid input {
  /* background: #5cd6c01a; */
  color: #27B274;
  border: 1px solid #27B274;
}

.invalid input {
  border: 1px solid #FF8080;
  color: #FF8080;
  /* background: #ed5f591a; */
}


button[type="button"] {
  position: absolute;
  right: 22px;
  background-color: transparent;
  border: 0px;
  cursor: pointer;
  color: #6F91BC;
  padding: 0;
}


.valid button {
  color: green;
}

.invalid button {
  color: red;
}

button[type="submit"] {
  padding: 15px 32px 15px 32px;
  background: linear-gradient(110.46deg, #70c3ff 12.27%, #4b65ff 93.92%);
  border: 0;
  border-radius: 30px;
  font-family: Inter;
  font-weight: 700;
  font-size: 16px;
  line-height: 14px;
  color: #ffffff;
  cursor: pointer;
  width: 240px;
  height: 48px;
}


p {
  font-size: 13px;
  font-weight: 400;
  line-height: 18px;
  margin: 0;
  color: #4A4E71;
  /* margin-left: 20px; */
}

p.valid {
  color: #009796;
}

p.invalid {
  color: #FF8080;
}
